.swiper-pagination-bullet {
  width: 14px !important;
  height: 14px !important;

  border: 1px solid #0404C6 !important;
  background: #FFFFFF !important;
  opacity: 1 !important;

  transition: all 0.3s ease-in-out;

  &:hover {
    border: 1px solid #6767FC !important;
    background: #6767FC !important;
  }

  &-active {
    background: #0404C6 !important;
  }
}

:root {
  --swiper-theme-color: #0404C6 !important;
  --swiper-navigation-size: 44px !important;
}
